.main-container {
  background: #0575e6;
  background: -webkit-linear-gradient(to left, #0575e6, #021b79);
  background: linear-gradient(to left, #0575e6, #021b79);
  width: 100%;
  position: relative;
  display: flex;
  justify-content: flex-end;
}

.app-container {
  background: white;
  margin: auto;
  text-align: center;
  width: 50%;
  height: 640px;
  margin-top: 20px;
  box-shadow: 6px -6px 5px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
}

.app_box {
  width: 40%;
  height: 100vh;
  margin-top: 20px;
}

.logo-img {
  width: 150px;
  padding: 20px 0 0 30px;
}

.header-txt {
  margin-top: 30px;
  font-weight: 700;
  font-size: 40px;
  text-align: center;

  color: #000000;
}

.header2-txt {
  font-weight: 600;
  font-size: 36px;
  text-align: center;
  padding-bottom: 35px;

  color: #021b3e;
}

.form__fields {
  padding: 5px 0px 15px 185px;
  font-weight: 500;
  font-size: 24px;
  width: 80%;
}

.field {
  width: 68%;
  height: 80%;
  border: 1px solid #000000;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
}

.fields {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 18px 0;
}

.app_img {
  margin: 30px 0 0 50px;
  align-items: center;
  width: 32%;
  position: absolute;
}

.btn-toRegister {
  font-weight: 500;
  font-size: 20px;
  text-decoration: none;
  font-weight: 500;
  font-size: 20px;
  background: #021b3e;
  color: white;
  text-decoration: none;
}

.btn-toRegister:hover {
  background: #20a3af;
  color: black;
}

.header-menu-login {
  background: #0575e6;
  width: 100%;
  padding: 10px 30px 0 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.forgot {
  font-size: 15px;
  font-weight: 300;
  cursor: pointer;
  padding-bottom: 20px;
}

.btn-lg {
  background: #021b3e;
  color: white;
  width: 200px;
}

.signin-btn {
  position: relative;
  display: flex;
  justify-content: center;
}

.btn-lg:hover {
  background: #71afff;
  color: black;
}

.register {
  cursor: pointer;
  position: relative;
  display: flex;
  justify-content: center;
  font-weight: 400;
  font-size: 20px;
  padding-top: 10px;
}

.form__fields1 {
  padding-left: 50px;
  font-weight: 500;
  font-size: 24px;
  width: 80%;
}

.fields1 {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
}

.page-txt {
  position: relative;
  top: 400px;
  padding: 0 50px 10px 50px;
  color: white;
  text-align: center;
}

.app-txt {
  position: relative;
  top: 400px;
  padding: 0 50px 10px 50px;
  color: white;
  text-align: center;
}

hr {
  width: 35%;
}

.or {
  font-weight: 500;
  font-size: 18px;
}

.lines {
  padding-top: 5px;
  position: relative;
  display: flex;
  justify-content: space-around;
}

.social-btn {
  padding-top: 5px;
  position: relative;
  display: flex;
  justify-content: space-around;
}

.social-login {
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 100px;
}
